.products-container {
    display:flex;
    margin-left:24px;
    flex-wrap: wrap;
}
.products {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.pagination {
    margin-top: 32px;
    width: 100%;
    justify-content: center;
    display: flex;
}
.prev, .next {
    font-size: 12px;
    padding: 7px 13px;
}
