#simple-tabpanel-1 > div {
    padding: 0px;
    padding-top: 24px;
}
.sidebarProducts-container {
    display:flex;
}
.titleSort-container {
    display: flex;
    justify-content: space-between;
}
.sort-title {
    padding-right:12px;
    margin-bottom: 0px!important;
}
.sort-container{
    display:flex;
    flex-direction: row;
    align-items: center;
}
.sort-select > .MuiFormControl-root > .MuiInputBase-formControl{
    height:35px;
    border-radius: 100px;
    background: #EFF1F1;
}
.sort-select > .MuiFormControl-root > .MuiInputBase-formControl > div {
    display: flex;
    align-items: center;
}
.sort-select {
    max-width: 170px;

}
.menu-item > span {
    font-size: 12px;
}
.menu-item {
    display: flex;
    align-items: center;
}
