.MuiCard-root {
    width: 312px;
    height: 476px;
    border: 1px solid #EFF1F1;
    flex-basis: 33.333333%
}
.card-root {
    box-shadow: none!important;
    margin-bottom:24px;
    margin-top: 0px;
    max-width: 30%;
}
.actionArea {
    width: 100%!important;
    height: 100%;
    padding: 24px!important;
}
.productImage {
    object-fit: contain!important;
}
.MuiCardContent-root {
    padding: 0px;
    margin-top: 16px;
}
.brand {
    color: #5C5F60;
    text-transform:uppercase;
    font-size: 10px!important;
    font-weight: 500;
    font-family: 'Roboto', sans-serif;
}
.title {
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
    color: #2D3132;
}
.cardFooter-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.oldPrice > span {
    color: #747778;
    text-decoration: line-through;
    font-size: 12px;
}
.newPrice > span {
    font-weight: 500;
    font-size: 14px;
    line-height: 150%;
    color: #2D3132;
}
.cardContent {
    padding: 0px!important;
}
.sellerInfo-container {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}
.sellerGeo {
    display: flex;
}
.sellerGeo > p {
    font-weight: 400;
    font-size: 12px;
    line-height: 150%;
}
.sellerName {
    display:flex;
    margin-bottom: 5px;
}
.sellerName > p {
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
}
.avatar {
    width: 20px!important;
    height: 20px!important;
    padding-right:8px;
}